import "./common/popovers/valueSelect.html";
import "./common/popovers/actionButtonEdit.html";

import addMessageButtonTpl from "./messages/addMessageButton.html";
import messageActionButtonTpl from "./messages/messageButton.tpl.html";

var uniqueId = function me() {
	me.ids || ( me.ids = {} )
	var newId;

	do {
		newId = Math.floor(Date.now() * Math.random());
	} while (me.ids[newId]);

	me.ids[newId] = newId;

	return newId;
};

var remove = (arr = [], item) => {
	var itemIndex = arr.indexOf(item);
	if (itemIndex === -1) return false;
	return arr.splice(itemIndex, 1);
};

export default angular.module('broadcastBuilder', [])

	.constant('MESSAGE_TYPES', {
		TEXT: 0,
		IMAGE: 1,
		GALLERY: 2
	})
	.constant('MESSAGE_ACTION_TYPES', {
		SEND_MESSAGE: 0,
		GO_TO_URL: 1,
	})

	.service('BroadcastMessagesService', function (Message) {
		var messages = [];

		return {
			getByParent: () => messages,
			addMessage: (type) => {
				if (typeof type !== "number") return false;
				var message = new Message(type);
				messages.push(message);
				return message;
			},
			removeMessage: (message) => {
				remove(messages, message);
			}
		}
	})

	.factory('Message', function (MESSAGE_TYPES) {
		function Message(type) {
			type || (type = MESSAGE_TYPES.TEXT);
			var message = this, data;
			data = (function (type) {
				switch (+type) {
					case MESSAGE_TYPES.TEXT:
						return {
							text: "",
							buttons: []
						};
					case MESSAGE_TYPES.IMAGE:
						return {
							title: "",
							description: "",
							url: "",
							imageSrc: "",
							buttons: []
						};
					case MESSAGE_TYPES.GALLERY:
						return {
							images: []
						};
				}
			})(type);

			angular.extend(message, {
				id: uniqueId(),
				type: type,
				data: data
			});

			return message;
		}

		return Message;
	})

	.factory('MessageActionButton', function (MESSAGE_ACTION_TYPES) {
		function MessageActionButton(data) {
			data || (data = {});
			var type = data.type || MESSAGE_ACTION_TYPES.SEND_MESSAGE;
			var messageActionButton = this;
			var defaults = {
				id: uniqueId(),
				type: type,
				title: "",
				goTourl: "",
				goToMessageId: 0
			};

			angular.extend(
				messageActionButton,
				defaults,
				data
			);

			return messageActionButton;
		}

		return MessageActionButton;

	})

	.factory('MessageImages', function () {
		class MessageImages extends Array {}

		return MessageImages;
	})
	.factory('MessageImageItem', function () {
		function ImageItem(data) {
			data || (data = {});
			var messageImageItem = this;
			return angular.extend(messageImageItem, {
				id: uniqueId(),
				title: "",
				description: "",
				url: "",
				imageSrc: "",
				buttons: []
			}, data);
		}

		return ImageItem;
	})

	.factory('SortableConfig',function () {

		function SortableConfig (data={}) {
			return angular.merge({
				group: {
					name: uniqueId(),
					pull: false,
					put: false
				},
				handle: ".action-drag-move",
				animation: 250,
				forceFallback: true,
				ghostClass: "sortable-ghost",  // Class name for the drop placeholder
				chosenClass: "sortable-chosen",  // Class name for the chosen item
				dragClass: "sortable-drag",  // Class name for the dragging item
				onChoose: function (evt) {
					evt.oldIndex;  // element index within evtnt
				},

				// Element dragging started
				onStart: function (evt) {
					console.warn('onStart', evt);
					evt.oldIndex;  // element index within parent
				},

				// Element dragging ended
				onEnd: function (evt) {
					console.warn('onEnd', evt);
					evt.oldIndex;  // element's old index within parent
					evt.newIndex;  // element's new index within parent
				},

				// Element is dropped into the list from another list
				onAdd: function (evt) {
					console.warn('onAdd', evt);
					evt.item;  // dragged HTMLElement
					evt.from;  // previous list
					// + indexes from onEnd
				},

				// Changed sorting within list
				onUpdate: function (evt) {
					console.warn('onUpdate', evt);
					evt.item;  // dragged HTMLElement
					// + indexes from onEnd
				},

				// Called by any change to the list (add / update / remove)
				onSort: function (evt) {
					console.warn('onSort', evt);
					// same properties as onUpdate
				},

				// Element is removed from the list into another list
				onRemove: function (evt) {
					console.warn('onRemove', evt);
					// same properties as onUpdate
				},

				// Attempt to drag a filtered element
				onFilter: function (evt) {
					console.warn('onFilter', evt);
					evt.item;  // HTMLElement receiving the `mousedown|tapstart` event.
				},

				// Event when you move an item in the list or between lists
				onMove: function (evt) {
					console.warn('onMove', evt);
					// Example: http://jsbin.com/tuyafe/1/edit?js,output
					// evt.dragged; // dragged HTMLElement
					// evt.draggedRect; // TextRectangle {left, top, right и bottom}
					// evt.related; // HTMLElement on which have guided
					// evt.relatedRect; // TextRectangle
					// originalEvent.clientY; // mouse position
					// return false; — for cancel
				},

				// Called when creating a clone of element
				onClone: function (evt) {
					evt.evtm; // original el
					evt.clone; // cloned el
				}
			}, data)
		}
		return SortableConfig;
	})

	.directive('broadcastBuilder', function () {
		return {
			template: require('./broadcastBuilder.tpl.html'),
			controller: 'BroadcastBuilderCtrl as vm',
			restrict: 'E',
			bindToController: true,
			scope: {}
		}
	})
	.controller('BroadcastBuilderCtrl', function (MESSAGE_TYPES, BroadcastMessagesService, SortableConfig) {
		var vm = this;

		vm.MESSAGE_TYPES = MESSAGE_TYPES;

		var messages = BroadcastMessagesService.getByParent();

		vm.id = uniqueId();
		vm.messages = messages;

		vm.messageTypesForSelect = [
			{ value: MESSAGE_TYPES.TEXT, title: "Add text card"},
			{ value: MESSAGE_TYPES.IMAGE, title: "Add image card"},
			{ value: MESSAGE_TYPES.GALLERY, title: "Add gallery"}
		];

		vm.onMessageTypeSelect = (msgType={}) => {
			vm.addMessage(msgType.value)
		};

		vm.sortableConfig = new SortableConfig();

		vm.addMessage = BroadcastMessagesService.addMessage;

		vm.getMessageType = function (type) {
			if (typeof type === "undefined")
				type = prompt('Тип сообщения (0 | 1 | 2):');
			if (!type) return undefined;
			type -= 0;
			if (typeof type === "number" && !isNaN(type))
				return type;
		};

		vm.startBroadcast = () => {
			console.warn('MESSAGES:',angular.toJson(messages,true));
		};

		// // TODO remove before release
		// vm.addMessage(MESSAGE_TYPES.TEXT);
		// vm.addMessage(MESSAGE_TYPES.IMAGE);
		// vm.addMessage(MESSAGE_TYPES.GALLERY);

		return vm;
	})

	.component('messageText', {
		template: require('./messages/messageText.tpl.html'),
		controller: 'MessageTextCtrl as vm',
		bindings: {
			message: "="
		}
	})
	.controller('MessageTextCtrl', function ($timeout,MESSAGE_TYPES, Message, MessageActionButton, BroadcastMessagesService) {
		var message = this.message;
		var buttonsList = message.data.buttons;

		this.id = uniqueId();
		this.removeMessage = () => {
			BroadcastMessagesService.removeMessage(message);
		};

		this.addButton = function (data) {
			var newButton = new MessageActionButton(data);
			newButton._isEdit = true;
			buttonsList.push(newButton)
		};
	})


	.component('messageImage', {
		template: require('./messages/messageImage.tpl.html'),
		controller: 'MessageImageCtrl as vm',
		bindings: {
			message: "="
		}
	})
	.controller('MessageImageCtrl', function (MESSAGE_TYPES, Message, MessageImageItem, BroadcastMessagesService) {
		var vm = this;
		var message = vm.message;

		vm.id = uniqueId();
		vm.removeMessage = () => {
			BroadcastMessagesService.removeMessage(message);
		};

		return vm;
	})


	.component('messageGallery', {
		template: require('./messages/messageGallery.tpl.html'),
		controller: 'MessageGalleryCtrl as vm',
		bindings: {
			message: "="
		}
	})
	.controller('MessageGalleryCtrl', function (MESSAGE_TYPES,BroadcastMessagesService, Message, MessageImageItem, MessageActionButton, SortableConfig) {
		var vm = this;

		var message = vm.message;
		var images = message.data.images;

		// var imagesSplice = images.splice;
		// images.splice = (from,to,el) => {
		// 	if (!el) {
		// 		console.warn('SORTABLE STRANGE BUG!!');
		// 		return false;
		// 	}
		// 	return imagesSplice.call(images,from,to,el);
		// };


		if (!images) console.warn("no images", vm);

		vm.id = uniqueId();

		vm.sortableConfig = new SortableConfig();

		vm.removeMessage = () => {
			BroadcastMessagesService.removeMessage(message);
		};
		vm.addButton = (currentImage, buttonData) => {
			var newButton = new MessageActionButton(buttonData);
			newButton._isEdit = true;
			try {
				currentImage.buttons.push(newButton)
			} catch (err) {
				console.warn("addButton err:", err)
			}
		};

		vm.addImage = () => {
			console.warn('add image');
			var newImage = new MessageImageItem();

			newImage.title = images.length; // TODO remove before release

			images.push(newImage);
		};
		vm.removeImage = (image) => {
			remove(images,image);
		};

		vm.addImage();

		return vm;
	})


	.component('messageButton', {
		template: messageActionButtonTpl,
		bindings: {
			button: "=",
			array: "="
		},
		controller: function ($timeout) {
			this.id = uniqueId();
			this.removeButton = (data) => {
				remove(this.array, data);
			};
			this.saveButton = () => {
				this.isEdit = false;
			};

			if (this.button._isEdit) { // TODO find better solution
				$timeout(()=>{
					this.isEdit = true;
					delete this.button._isEdit
				})
			}
		}
	})

	.component('addButton', {
		template: addMessageButtonTpl,
		bindings: {
			values: "=",
			method: "="
		},
		controller: function () {
			this.id = uniqueId();
			this.select = (selectedValue) => {
				this.method(selectedValue);
			};
		}
	})
;



